import React, { Component, Suspense, memo } from "react";
import { detectMobile } from '../../../../utils'
import './scss/Exhibit.scss'
import { isSocialEmbed } from "../../../../actions/api";
import Masonry from 'react-masonry-component';
import Card from './Elements/Card'
const SwiperFalling = React.lazy(() => import("./Elements/SwiperFalling"));
class Exhibit extends Component {
    state = {
        activeID: -1,
        newPostdata: [],
        postData: []
    }
    componentWillMount() {
        this.onLoadData(this.props)
    }
    onLoadData = (props) => {
        const { cardNumber, postData, completeDataObject, adjustWidth, wall, postSize, clickToShowPopUp, onClickToCTA, postSizeInPx } = props;
        const { activeID } = this.state
        const column = wall.ThemeRule.numberOfCoumn;
        const newPostdata = Array.from({ length: column }, () => []);
        postData.forEach((post, index) => newPostdata[index % column].push(post));
        newPostdata.forEach(col => {
            while (col.length < 3) {
                const randomCol = newPostdata[Math.floor(Math.random() * column)];
                const randomItem = randomCol[Math.floor(Math.random() * randomCol.length)];
                col.push(randomItem);
            }
        });
        this.setState({ newPostdata, postData })
    }
    scrollInterval = null
    componentDidMount = () => {
        this.scrollInterval = setInterval(() => this.onUpdateTimeInterVal(), 3000);
        const { heightUpdate } = this.props;
        setTimeout(() => heightUpdate(), 1000)
    };
    onUpdateTimeInterVal = () => {
        const { activeID } = this.state;
        const { cardNumber, wall } = this.props
        const column = wall.ThemeRule.numberOfCoumn;
        const nextID = (parseInt(activeID) + 1);
        this.setState({ activeID: parseInt(column) >= nextID ? nextID : 0 })
    };
    componentWillUnmount() {
        if (this.scrollInterval) clearInterval(this.scrollInterval);
    }
    componentWillReceiveProps(nextProps) {
        if (JSON.stringify(nextProps.postData) !== JSON.stringify(this.props.postData)) {
            this.onLoadData(nextProps);
            return true
        }

    }
    // componentDidUpdate(prevProps, prevState) {
    //     if (JSON.stringify(this.props.postData) !== JSON.stringify(prevProps.postData)) {
    //         this.onLoadData();
    //         return true
    //     }
    //     else if (this.state.activeID != prevState.activeID) return true;
    //     else return false
    // }

    render() {
        const { cardNumber, postData, completeDataObject, adjustWidth, wall, postSize, clickToShowPopUp, onClickToCTA, postSizeInPx } = this.props;
        const { activeID, newPostdata } = this.state
        // console.log("one", newPostdata.length, postData.length);
        return <> {isSocialEmbed ? <Masonry
            id="pixel"
            className={`tb_w_e_post_container`}
            tabIndex='0'
            aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`}
            elementType={'div'}
            options={{
                transitionDuration: 0,
                percentPosition: true,
                itemSelector: '.tb_w_e_post_wrapper',
                horizontalOrder: true,
                resize: true,
            }}
            disableImagesLoaded={false}
            updateOnEachImageLoad={false}
            imagesLoadedOptions={{}}
            enableResizableChildren={true}>

            {(postData?.length > 0) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <Card ownerId={wall.Wall.owner} itemData={cardData} key={`sqr_crd_${index}_${cardData.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} />
            })}

        </Masonry>
            :
            <div className="tb_w_e_post_container">
                {
                    newPostdata.map((item, index) => {
                        return <Suspense fallback={<></>}>
                            <SwiperFalling
                                key={`exhi_${index}_${item?.length}`}
                                pIndex={index}
                                itemIds={item}
                                activeID={activeID}
                                completeDataObject={completeDataObject}
                                wall={wall}
                                onClickToCTA={onClickToCTA}
                                postSizeInPx={postSizeInPx}
                                cardCount={newPostdata.length}
                                clickToShowPopUp={clickToShowPopUp}
                            />
                        </Suspense>
                    })
                }

            </div>}
        </>
    }
}



export default memo(Exhibit)
