import React, { Suspense } from 'react';
import Time from "../../../Elements/Time/Time"
import Network from "../../../Elements/Network/Network"
import Author from '../../../Elements/Author/Author'
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font, rating, ownerId, instaHash }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  const networkIcon = (network.id !== null && network.id !== '') && ThemeRule.socialAction ? true : false
  return <div className="tb_w_e_author_wrapper">
    {postAuthor ? <Suspense fallback={<></>}><Author network={network} author={author} authorClass={'tb_w_e_author_profile'} /></Suspense> : ''}
    <div className="tb_w_e_author_info">
      {postAuthor ? <div className={`tb_w_e_authorname tb-cTBfont-${ThemeRule.font_varient}`}>{author.name}</div> : ''}
      {<div className="tb_w_e_post_info">
        {postAuthor ? <div className="tb_w_e_username">{username}</div> : ''}
        {authorSeprator ? <div className="tb_w_e_seprator"> </div> : ''}
        {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_w_e_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /></Suspense> : ''}
      </div>}
    </div>
    {networkIcon  ?
      <div className="tb_w_e_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_w_e_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div>
      : null}
  </div>
}
export default AuthorDetails