import React, { Suspense } from "react";
import Video from "../../../Elements/Media/Video";
import { isTagEmbed, isTagShop,isSocialEmbed  } from "../../../../../actions/api";
import Image from "../../../Elements/Media/Image";
const Like = React.lazy(() => import('../../../Elements/Reactions/Like'));
const Media = ({ itemData, wallID, ownerId, wall, isCover, ThemeRule }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = false
  const mediaIcon = itemData.hotspot ? false : true
  return <div className="tb_w_e_media_wrap">
    {isSocialEmbed && wall.Personalization.interactive ? <Suspense fallback={<></>}><Like postId={itemData.id} wallID={wallID} UserDetail={wall.UserDetail} /></Suspense> : null}
    {autoPlay ?
      <Video VideoClass="tb_w_e_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} wall={wall} size={size} isCover={isCover} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_w_e_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} hotspot={true} />
    }
  </div>
}
export default Media